.footer {
    background-color: #2c3e50;
    color: #ffffff;
    padding: 20px 0;
    position: relative;
    bottom: 0;
    width: 100%;
    text-align: center;
}

.footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.social-icons {
    margin-bottom: 10px;
}

.social-icons a {
    color: #ffffff;
    margin: 0 10px;
    font-size: 24px;
    transition: color 0.3s;
}

.social-icons a:hover {
    color: #1abc9c;
}

a {
    color: #fffb85;
    text-decoration: none;
}

a:hover {
    color: #53ffdd;
}

@media (max-width: 768px) {
    .footer-content {
        padding: 0 20px;
    }

    .social-icons a {
        margin: 0 5px;
        font-size: 20px;
    }
}